.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Responsive Layout */
.main-container {
  display: flex;
  align-items: flex-start;
  gap: 32px;
  max-width: 1400px;
  margin: 0 auto;
  flex-direction: row;
}

.side-panel {
  flex: 0 0 300px;
  background: #f8fafc;
  border-radius: 16px;
  padding: 20px;
  box-shadow: 0 4px 16px rgba(0,0,0,0.1);
  height: 280px;
  display: flex;
  flex-direction: column;
}

.center-content {
  flex: 1;
  text-align: center;
}

/* Mobile Styles */
@media (max-width: 768px) {
  .main-container {
    flex-direction: column;
    gap: 20px;
    padding: 20px;
  }
  
  .side-panel {
    flex: none;
    width: 100%;
    height: auto;
    max-height: 200px;
  }
  
  .center-content {
    order: -1; /* Move center content to top on mobile */
  }
  
  .App-header h1 {
    font-size: 28px !important;
  }
  
  .App-header p {
    font-size: 16px !important;
  }
  
  /* Hide corner family images on mobile */
  .family-corner {
    display: none;
  }
  
  /* Make buttons stack on mobile */
  .button-container {
    flex-direction: column;
    gap: 12px !important;
  }
}
